body {
}

.App {
  font-family: sans-serif;
  text-align: left;
}

h1 {
  font-size: 4em;
  font-weight: 900;
}

.chicken {
  width: 70px; /* width of container */
  height: 70px; /* height of container */
  object-fit: cover;
  object-position: 1px 5px; /* try 20px 10px */
  margin-top: -3px;
  margin-bottom: 25px;
}

.chickenTitle {
  float: left;
  width: 100%;
  padding: 5%;
  padding-top: 0%;
  color: #555;
}

.chickenTitle2 {
  float: left;
  width: 100%;
  padding-left: 5%;
  padding-top: 1%;
  color: #555;
}

.chickenTitle2 h3 {
  float: left;
}
.chickenTitle2 p {
  float: left;
  padding-top: 7px;
  padding-left: 6px;
  font-style: italic;
}

.chickenTime {
  padding-left: 5%;
  margin-top: -2%;
}

.chickenHeader {
  background-image: linear-gradient(white, #debaba35);
  min-height: 100%;
  min-width: 100%;
  padding: 5%;
  padding-bottom: 8%;
}
.winner {
  padding: 5%;
  padding-top: 3%;
}

h4 {
  font-size: 1em;
}

.progress {
  margin-top: 30px;
  width: 40%;
  float: left;
}

.question {
  float: left;
  padding: 5%;
  padding-top: 2%;
  padding-bottom: 2%;
}

.status {
  width: 59%;
  height: 120px;
  margin-left: 1%;
  float: left;
}

#next {
  margin-top: 20px;
}

.progress-bar {
  background-color: #6e0e0a;
}

.progress {
  background-color: #f0f0c9;
}

.form-check-input:checked {
  background-color: #d74e09;
  border-color: #d74e09;
}

.btn-primary,
.btn-primary:hover,
.btn-primary:active,
.btn-primary:visited {
  background-color: #f2bb05 !important;
  border-color: #f2bb05 !important;
}

.btn-primary {
  background-color: #d74e09 !important;
  border-color: #d74e09 !important;
}

.btn-primary:focus,
.btn-primary.focus {
  box-shadow: 0 0 0 4px #f0f0c9;
}

.form-check-input:focus {
  box-shadow: 0 0 0 4px #f0f0c9;
  border-color: #f2bb05;
}

.btn-secondary,
.btn-secondary:hover,
.btn-secondary:active,
.btn-secondary:visited {
  background-color: #1d9bf0 !important;
  border-color: #1d9bf0 !important;
}

.btn-secondary {
  background-color: #1d9bf0 !important;
  border-color: #1d9bf0 !important;
  margin-left: 2%;
}

.btn-secondary:focus,
.btn-secondary.focus {
  box-shadow: 0 0 0 4px #f0f0c9;
}
